<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process111"></a>

<p><br></p>
<p><span class="rvts17">蹊跷的谋杀（</span><span class="rvts50">The Murderous Gheist</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">起始坐标：</span>
<br><span class="rvts20">X:188,Y:196</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：护法者亚罗（</span><span class="rvts20">Magister Yarrow</span><span class="rvts21">）</span>
<br><span class="rvts27"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">寻找亚罗的父亲米果（</span><span class="rvts20">Migo</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1012.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">在进入欢乐堡大门左手边的城墙上，和护法者亚罗谈话，她会让你区寻找她的父亲米果。</span>
<br><span class="rvts20"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1013.jpg" display="block" max-width="100%"><span class="rvts20"></span>
<br><span class="rvts21">米果的位置位于贫民窟山洞后边的海岸处，坐标位置为</span><span class="rvts20">X</span><span class="rvts21">：</span><span class="rvts20">256牋Y</span><span class="rvts21">：</span><span class="rvts20">80</span><span class="rvts21">。米果由于受到了某种死灵法术的袭击，变得心智不太正常，他有食尸的迹象。</span>
<br><span class="rvts21">可以通过交给他一朵亚落花（</span><span class="rvts20">Yarrow Flower</span><span class="rvts21">），进行物物交换，换回他身上的戒指，作为信物，并返回到护法者亚罗那里。也可以直接杀掉米果，拿着戒指找亚罗。并且可以编谎话称发现米果的时候，他就已经死了。不过该任务最好的完成方法是，让米果活着，并让他们父女见面，这样做，可以获得</span><span class="rvts20"></span><span class="rvts21">英雄</span><span class="rvts20"></span><span class="rvts21">的标签。</span>
<br><span class="rvts21">最后，无论怎样完成任务，即便杀掉亚罗和米果，都会获得牢房钥匙。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



